.linkLeft {
  text-decoration: none;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 5.5vw;
  padding-left: 6vw;
  padding-right: 3vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border: 1vmin solid #ffffff;
  width: 20vw;
  height: 12vh;
  letter-spacing: 0.05em;
  text-align: right;
}

.linkRight {
  text-decoration: none;
  color: white;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 5.5vw;
  padding-left: 3vw;
  padding-right: 6vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border: 1vmin solid #ffffff;
  width: 20vw;
  letter-spacing: 0.05em;
  text-align: left;
  height: 12vh;
}

.flipTitile {
  font-size: 6vw;
  color: transparent;
  display: flex;
  padding-left: 5.5vw;
  padding-right: 3vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border: 1vmin solid transparent;
  width: 20vw;
  letter-spacing: 0.05em;
  text-align: right;
  height: 12vh;
}

.background {
  width: 100vw;
  height: 100vh;
  background-image: url(../../assets/images/bg.jpg);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.wrapLeft {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  height: 50vh;
}

.wrapRight {
  display: flex;
  flex-direction: column;
  height: 50vh;
  /* justify-content: space-between; */
}

.gameWrap {
  position: absolute;
  display: flex;
  width: 4vmax;
  height: 6vmin;
  justify-content: center;
  align-items: center;
  left: 48vw;
  bottom: 15vh;
}
.play {
  width: 2vmax;
  height: 3vmin;
}
.rectangle {
  width: 4vmax;
  height: 6vmin;
  background: #ff0000;
  mix-blend-mode: multiply;
  position: absolute;
  top: 0;
  left: 0;
}

.mat1 {
  width: 25%;
  height: 25%;
  margin-right: 2vw;
  margin-bottom: 5vh;
}
.mat2 {
  width: 25%;
  height: 25%;
  margin-left: 2vw;
  margin-top: 3vh;
}

#filter-campaign {
  position: absolute;
  width: 31vw;
  height: 15vh;
  right: 65vw;
  bottom: 18vh;

  background: #0066ff;
  mix-blend-mode: multiply;
}

#filter-project {
  position: absolute;
  width: 31vw;
  height: 15vh;
  left: 65vw;
  top: 18vh;

  background-color: #ff00e5;
  mix-blend-mode: multiply;
}
.filter-logo {
  position: absolute;
  left: 51%;
  right: 0%;
  top: 10%;
  bottom: 40.94%;

  background: #ffff00;
  mix-blend-mode: multiply;
}